'use strict';

(function () {
  let buyLinks = document.querySelectorAll('.countries-information__buy-link');
  let buyPopup = document.querySelector('.buy-tour__popup');
  let closeBuyPopup = buyPopup.querySelector('.buy-tour__close');
  let buyTour = document.querySelector('.buy-tour')

  buyLinks.forEach(function (elem) {
    elem.addEventListener('click', function (evt) {
      evt.preventDefault();
      if (document.querySelector('.buy-tour__wrapper')) {
        document.querySelector('.buy-tour__wrapper').removeAttribute('style');
      } else {
        let wrapper = document.createElement('div');
        wrapper.classList.add('buy-tour__wrapper');
        buyTour.append(wrapper);
      }
      setTimeout(() => {
        buyPopup.classList.add('buy-tour__popup--active');
      }, 20);
    })
  });

  closeBuyPopup.addEventListener('click', function (evt) {
    evt.preventDefault();
    buyPopup.classList.remove('buy-tour__popup--active');
    document.querySelector('.buy-tour__wrapper').style = 'transition: opacity 3s, 0s top 3s'
  })
})();
